@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html,
body,
#root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,
h2,
h3,
p {
  margin: 0;
}
input,
textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading,
.error {
  text-align: center;
  margin: 40px auto;
}

.searchbar {
  font-size: 1rem;
}

.searchbar form {
  display: flex;
  align-items: center;
}
.searchbar input {
  margin-left: 10px;
  width: 20rem;
}

.navbar {
  background: #58249c;
  padding: 1.2rem;
  color: #fff;
}
.navbar nav {
  display: flex;
  align-items: center;
  max-width: 80rem;
  margin: 0 auto;
}
.navbar a.brand {
  margin-right: auto;
  color: #fff;
  text-decoration: none;
  letter-spacing: 3px;
  font-weight: 100;
  font-size: 1.2rem;
}
.navbar .link {
  color: #fff;
  text-decoration: none;
  margin-left: 40px;
  padding: 8px;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 1rem;
}
.navbar .link:hover {
  background: #fff;
  color: #333;
}
.navbar .logout img {
  width: 1.2rem;
  height: 1.2rem;
  background-color: transparent;
  cursor: pointer;
  margin-left: 2rem;
}

.recipe-list {
  /* display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 1200px;
  margin: 40px auto; */
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  grid-row-gap: 2rem;
  row-gap: 2rem;
  max-width: 900px;
  padding-bottom: 100px;
}

.recipe-item .card {
  background: #fff;
  color: #333;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
  /* font-size: 1rem; */
  display: flex;
  justify-content: space-between;
}

.recipe-item .card:hover {
  /* transform: rotate(3deg); */
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.recipe-item .card h3 {
  color: #555;
  margin-bottom: 6px;
}

.recipe-item .card p {
  color: #999;
  font-size: 0.9em;
}

.recipe-item .card .info {
  color: #555;
  font-size: 0.7em;
  margin: 5px 0 0 0;
  line-height: 1.5em;
  width: auto;
  margin-right: 20px;
  display: flex;
  flex-direction: column;
}

.recipe-item .card .links {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  margin-top: auto;
}

.recipe-item .card a {
  color: #555;
  text-decoration: none;
  background: #e2e2e290;
  font-size: 0.9rem;
  text-align: center;
  width: 5rem;
  padding: 5px;
  border-radius: 4px;
}

.recipe-item .card a:hover {
  background-color: #e2e2e2;
}

.recipe-item .card .delete {
  cursor: pointer;
  -webkit-filter: invert(60%);
          filter: invert(60%);
  max-width: 2rem;
  max-height: 2rem;
}

.recipe-item .card .img {
  margin: 0 0 0rem 0;
  /* max-width: 40%; */
  display: flex;
  justify-content: center;
  /* width: fit-content; */
}

.recipe-item .card .img img {
  border-radius: 0.5rem;
  max-width: 400px;
  max-height: 250px;
}

/* dark mode */
.recipe-item .card.dark {
  background: #555;
}
.recipe-item .card.dark p,
.recipe-item .card.dark h3,
.recipe-item .card.dark div {
  color: #e4e4e4;
}

.home {
  max-width: 1200px;
  margin: 60px auto;
}

.create {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
}
.create label span {
  display: block;
  margin: 30px 0 10px;
}
.create p {
  margin-top: 10px;
  font-size: 0.8em;
}
.create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}

.ingredients {
  display: flex;
  align-items: center;
}

.ingredients button {
  margin: 0 0 0 10px;
}

/* dark mode */
.recipe.dark {
  background: #555;
  color: #e4e4e4;
}
.create label span.dark {
  color: #e4e4e4;
}
.create p.dark {
  color: rgb(137, 137, 137);
}
.create h2.dark {
  color: #e4e4e4;
}

.page-title {
  font-size: 1.5rem;
}

.recipe {
  max-width: 900px;
  border-radius: 0.2rem;
  margin: 40px auto;
  text-align: center;
  background: #fff;
  padding: 40px 40px 120px 40px;
  box-sizing: border-box;
}
.recipe .image {
  max-width: 70%;
}
.recipe small {
  text-align: right !important;
}
.recipe ul {
  display: block;
  padding: 3rem;
  margin: 3rem auto 2rem;
  background-color: #f3f3f3;
  border-radius: 0.5rem;
  width: 50%;
}
.recipe li {
  list-style-type: none;
  margin-right: 10px;
  color: rgb(137, 137, 137);
  text-align: center;
  font-size: 1.2rem;
}
li:not(:last-child) {
  margin-bottom: 0.6rem;
}
.recipe li::after {
  content: ', ';
}
.recipe li:last-child::after {
  content: '.';
}
.recipe .method {
  text-align: left;
  line-height: 1.5em;
  font-size: 1.3rem;
  white-space: pre-line;
  white-space: pre-wrap;
}
.recipe button {
  display: inline-block;
  width: 5.5rem;
  font-size: 0.9rem;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 3rem 0.5rem 0;
  text-align: center;
  float: right;
}

/* dark mode */
.recipe.dark {
  background: #555;
  color: #e4e4e4;
}

.footer {
  width: 100vw;
  background-color: #555;
  padding: 10px;
  position: fixed;
  bottom: 0;
}

.footer p {
  text-align: center;
  color: #f1f1f1;
  font-size: 1rem;
}

/* dark mode */
.footer.dark {
  color: #f1f1f1;
  background-color: #555;
}

.update {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
  font-size: 1rem;
  padding-bottom: 4rem;
}
.update label span {
  display: block;
  margin: 30px 0 10px;
}
.update p {
  margin-top: 10px;
  font-size: 0.8em;
}
.update button {
  display: block;
  width: 100%;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 50px auto;
}

.update input {
  font-family: inherit;
}

.update textarea {
  height: 30rem;
  font-family: inherit;
}

.ingredients {
  display: flex;
  align-items: center;
}

.ingredients button {
  margin: 0 0 0 10px;
}

.authentication {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
  font-size: 1rem;
  padding-bottom: 4rem;
}
.authentication label {
  display: block;
  margin: 30px 0 10px;
}
.authentication p {
  margin-top: 10px;
  font-size: 0.8em;
}
.authentication button {
  display: block;
  width: 100%;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 2rem auto 0;
}
.authentication button.toggle-login {
  background-color: transparent;
  color: #58249c;
  margin-top: 0.5rem;
}
.authentication button.toggle-login:hover {
  background-color: #58249c;
  color: #fff;
}
.authentication input {
  font-family: inherit;
}

.App {
  min-height: 100%;
  padding-bottom: 3rem;
}

.App.dark {
  background: #333;
}

.theme-selector {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  max-width: 1200px;
  margin: 20px auto;
}

.theme-buttons div {
  display: inline-block;
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-left: 15px;
  border-radius: 50%;
}

.mode-toggle {
  margin-right: auto;
}

.mode-toggle img {
  width: 1.4rem;
  height: 1.4rem;
  cursor: pointer;
}

